import React from 'react';
import 'tachyons';

const Card = (props)=>{

return(

		<div className='tc bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5' >
			<img alt='robo' src={`https://robohash.org/${props.id}?200x200`} />
			<h2>{props.name}</h2>
			<h3>{props.email}</h3>

		</div>

	);

}

export default Card;